@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}
* {
  font-family: "Roboto Condensed", sans-serif;
}
#bg {
  background: #f8f9fa;
}
.section-heading {
  margin-left: 5.3rem;
  font-weight: 700;
  margin-top: 1rem;
}
#projects {
  margin-bottom: 2rem;
}
@media (max-width: 430px) {
  .section-heading {
    margin-left: 1.5rem;
  }
}

/***************************************** Navbar *****************************************************/
nav {
  position: fixed !important;
  width: 100%;
  z-index: 3;
}
nav a.nav-link {
  font-size: 0.8rem;
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-left: 1rem;
  margin-right: 1rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-bottom: 0.13rem solid rgba(255, 255, 255, 0);
  border-bottom: 0.13rem solid var(--primary-dark);
}
.navbar-brand {
  color: #fff !important;
  color: var(--primary-white) !important;
  font-size: 1.5rem !important;
  font-weight: 500;
}
.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
  outline: none !important;
  box-shadow: none !important;
}
.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-bottom: 0.13rem solid #f9ab00;
  border-bottom: 0.13rem solid var(--primary-orange);
}
.fa-bars {
  color: #fff;
  color: var(--primary-white);
}

/***************************************** Header *****************************************************/
.header-wrapper {
  position: relative;
  background: url(https://www.wallpapertip.com/wmimgs/83-838362_web-developer.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.main-info {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

@media (max-width: 768px) {
  .typed-text {
    font-size: 1.7rem;
  }
}
.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}
.btn-download-CV {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  text-decoration: none;
  z-index: 2;
  margin-top: 1.25rem;
}
.btn-download-CV:hover {
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: 0.2s ease-in-out;
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles {
    display: none;
  }
}

/***************************************** About *****************************************************/

#about-section {
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background: rgba(255, 255, 255, 1);
}
.about-wrapper {
  display: flex;
}
.left-about {
  width: 45%;
  box-shadow: -20px 20px 69px -27px rgb(0 0 0 / 20%);
  background: rgba(128, 128, 128, 0.3);
}
.left-about img {
  width: 100%;
  height: 100%;
}
.right-about {
  width: 55%;
}
.about-text-wrapper {
  padding-left: 2.5rem;
  padding-top: 3rem;
}
#intro-heading {
  font-size: 1.3rem;
  font-weight: 700;
  color: #b1b493;
}
#desc {
  color: #999;
}
.about-list {
  list-style: none;
  padding-left: 0;
  font-size: 1.2rem;
  margin-bottom: 40px;
}
.about-list li {
  display: flex;
  margin: 1rem 0 1rem 0;
}
.list-name {
  margin-right: 1rem;
  width: 150px;
  font-weight: 600;
}
.list-desc {
  margin-left: 1rem;
  width: 250px;
  color: #b1b493;
}
#hobbies {
  display: flex;
}
.hobby {
  width: 120px;
  display: flex;
  align-items: center;
}
.hobby p {
  margin: 0;
  margin-left: 5px;
  font-weight: 700;
}
.vertical {
  border-left: 3px solid #b1b493;
  height: 30px;
  margin-right: 5px;
}
.circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #b1b493;
  display: flex;
  align-items: center;
  justify-content: center;
}
.circle i {
  color: white;
}

@media (max-width: 430px) {
  .about-wrapper {
    flex-direction: column;
  }
  .left-about {
    width: 100%;
  }
  .right-about {
    width: 100%;
  }
  .about-list li {
    justify-content: space-between;
  }
  .about-text-wrapper {
    padding-left: 1rem;
  }
  .list-desc {
    margin: 0;
  }
  .list-name {
    margin: 0;
  }
}
.card-text {
  color: #999;
}
.card-body {
  max-height: 310px !important;
  min-height: 310px !important;
  position: relative;
}
.web-btn {
  background-color: #b1b493 !important;
  border: none !important;
  position: absolute;
  bottom: 25px;
  left: 15px;
}
.web-btn:focus,
.web-btn:active,
.web-btn:focus {
  outline: none !important;
  box-shadow: none !important;
}
.send-btn {
  background-color: #b1b493 !important;
  border: none !important;
}
.send-btn:focus,
.send-btn:active,
.send-btn:focus {
  outline: none !important;
  box-shadow: none !important;
}

.ftco-footer {
  font-size: 16px;
  background: #232931;
  padding: 7em 0;
  z-index: 0;
  margin-top: 30px;
}
.ftco-section {
  padding: 30px;
}
.ftco-footer .ftco-footer-widget h2 {
  font-weight: normal;
  color: #fff;
  margin-bottom: 40px;
  font-size: 22px;
  font-weight: 600;
}
.ftco-footer p {
  color: rgba(255, 255, 255, 0.7);
}
.list-unstyled {
  padding-left: 0;
  list-style: none;
}
.ftco-footer .ftco-footer-widget ul li {
  margin-bottom: 10px;
}
.ftco-footer .ftco-footer-widget ul li a {
  color: rgba(255, 255, 255, 0.6);
  text-decoration: none;
}
.block-23 ul {
  list-style: none;
  padding-left: 0;
}
.block-23 ul li .icon {
  width: 40px;
  font-size: 18px;
  padding-top: 2px;
  color: white;
}
.block-23 ul li .icon,
.block-23 ul li .text {
  display: table-cell;
  vertical-align: top;
}
.block-23 ul li span {
  color: rgba(255, 255, 255, 0.7);
}
.ftco-footer-social {
  display: flex;
}
.ftco-footer-social li {
  margin-right: 20px;
}
.ftco-footer-social li a i {
  font-size: 30px;
}

